import React from "react";
import {Field, reduxForm, change} from "redux-form";
import {Redirect} from "react-router-dom";
import {max_letngtcreator, required} from "../../utils/validators/validators";
import clas from "./messege.module.css"

const Mesege = (props) => {

    /** every new message**/
    let meseges = props.masage_data.map((m) =>
        <div className={clas.oneMessage}>
            <span>{m.messege_item}</span>
        </div>)
    /** add message**/
    let add_mesege_form = (values) => {
        props.add_masage(values.changet_mesege)
    }
    if (!props.isOuth) {
        return <Redirect to={'/Login'}/>
    }
    return (
        <div className={clas.Massege}>

            <div>
                <Mesege_form_Redux onSubmit={add_mesege_form}/>
                {meseges}
            </div>
        </div>
    )
}
/** maxlength of input**/
const maxLength100 = max_letngtcreator(100)

const Mesege_form = (props) => {


    return <div>
        <form name='myFormName' onSubmit={props.handleSubmit }>

            <div className={clas.writeMassage}>
                <div>
                    <Field placeholder={'new message'}
                           name={'changet_mesege'} component='textarea'
                           validate={[required, maxLength100]}
                    />
                </div>
                <div className={clas.buttone}>
                    <button>go</button>
                </div>
            </div>

        </form>

    </div>
}

const Mesege_form_Redux = reduxForm({form: "mesege_form"})(Mesege_form)

export default Mesege


