import React from "react";
import {addmesege_action_creator, change_masege_action_creator} from "../../../redux/dialog_reducer";
import Mesege from "./mesge";
import {connect} from "react-redux";
import {compose} from "redux";

const MapStateToProps = (state) => {
    return {
        masage_data: state.dialog_page.masage_data,
        isOuth:state.auth.isOuth,
        initialValues:'1111'
    }
}
const MapDispatchToProps = (dispatch) => {
    return {
        add_masage: (new_mesege_form) => {
            dispatch(addmesege_action_creator(new_mesege_form))
        },
        onChange_masage: (text) => {
            dispatch(change_masege_action_creator(text))
        }
    }
}

