import React from "react";
 import clas from "./dialog.module.css"





const Dialog=(props)=> {

    // let dialogs=
    //     props.dialog_page.dialog_data.map((d)=> <div>{d.name}</div>)

    let add_dialog=React.createRef()
    let add_dialog_item=()=>{
        alert("you are pseudo looking for a : " + add_dialog.current.value)
    }
    //
    return (
        <div >
            <div className={clas.dialog}>
                <div className={clas.lookingFriend} >
                    <input placeholder="find friend" name="" id="" cols="30" rows="2" ref={add_dialog}/>
                    <button onClick={add_dialog_item}> go</button>
                </div>
                <div className={clas.allItems}>
                    {props.dialog_page.dialog_data.map((d)=> <div ><span className={clas.oneItem}>{d.name}</span></div>)}
                </div>
            </div>


        </div>
    )
}
export default Dialog

